<template>
    <div class="page-supplier-add abs scroll-y">
        <div class="form-content">
            <div class="title b f-18">{{form.id ? '' : ''}}</div>
            <div class="flex-v-center">
                <input-box v-model="form.suppilerName" class="flex-item" label=""></input-box>
            </div>
            <div class="flex-v-center">
                <select-box v-model="form.nature" class="mr-20" label="" :list="natureList"></select-box>
                <input-box v-model="form.address" class="flex-item" label=""></input-box>
            </div>
            <div class="flex">
                <input-box v-model="form.website" class="flex-item mr-20" label=""></input-box>
                <input-box v-model="form.phone" class="flex-item mr-20" label=""></input-box>
                <input-box v-model="form.postcode" type="number" class="flex-item mr-20" label=""></input-box>
                <input-box v-model="form.fax" class="flex-item" label=""></input-box>
            </div>
            <div class="flex-v-center">
                <input-box v-model="form.legalPerson" class="mr-20" label=""></input-box>
                <input-box v-model="form.legalPersonPhone" label=""></input-box>
            </div>
            <div class="flex-v-center">
                <input-box v-model="form.totalFactoryArea" type="number" label=""></input-box>
                <span class="mr-20"></span>
                <input-box v-model="form.totalArchitArea" type="number" label=""></input-box>
                <span class="mr-20"></span>
                <select-box v-model="form.workType" label="" :list="workTypeList"></select-box>
            </div>
            <div class="flex-v-center">
                <input-box v-model="form.processRoute" class="flex-item" label=""></input-box>
            </div>
            <div class="flex-v-center">
                <input-box v-model="form.evaluation" class="flex-item" label=""></input-box>
            </div>
            <div class="flex-v-center">
                <input-box v-model="form.suppilerNo" class="mr-20" label="" style="width: 240px;"></input-box>
                <input-box v-model="form.suppilerPreparer" class="mr-20" label=""></input-box>
                <input-box v-model="form.responsiblePerson" label=""></input-box>
            </div>
            <div class="f-14 c-6 b" style="margin: 25px 0 0;"></div>
            <div class="flex-v-center">
                <input-box v-model="form.staffInfo.totalWorkers" type="number" label=""></input-box>
                <span class="mr-20"></span>
                <input-box v-model="form.staffInfo.managerWorkers" type="number" label=""></input-box>
                <span class="mr-20"></span>
                <input-box v-model="form.staffInfo.productionWorkers" type="number" label=""></input-box>
                <span class="mr-20"></span>
                <input-box v-model="form.staffInfo.technicistWorkers" type="number" label=""></input-box>
                <span></span>
            </div>
            <div>
                <div class="f-14 c-6 b" style="margin: 25px 0 10px;"></div>
                <div class="credentials-form">
                    <check-box v-model="form.credentialsInfo.charter" name="credentials" text=""></check-box>
                    <check-box v-model="form.credentialsInfo.tax" name="credentials" text=""></check-box>
                    <check-box v-model="form.credentialsInfo.legalPersonCode" name="credentials" text=""></check-box>
                    <check-box v-model="form.credentialsInfo.bankOpenAccount" name="credentials" text=""></check-box>
                    <check-box v-model="form.credentialsInfo.credibilityLetter" name="credentials" text=""></check-box>
                    <check-box v-model="form.credentialsInfo.manufacturerAuthorization" name="credentials" text=""></check-box>
                    <check-box v-model="form.credentialsInfo.productCertification" name="credentials" text=""></check-box>
                    <check-box v-model="form.credentialsInfo.productPermit" name="credentials" text=""></check-box>
                    <check-box v-model="form.credentialsInfo.importAndExportCertification" name="credentials" text=""></check-box>
                    <check-box v-model="form.credentialsInfo.otherCertification" name="credentials" text=""></check-box>
                </div>
            </div>
            <div>
                <div class="f-14 c-6 b" style="margin: 25px 0 0;"></div>
                <div class="flex-v-center">
                    <check-box v-model="form.qualityAssuranceInfo.buildQualitySystem" name="qualityAssurance" class="mr-20" text=""></check-box>
                    <check-box v-model="form.qualityAssuranceInfo.passThirdAttestation" name="qualityAssurance" class="mr-20" text=""></check-box>
                    <input-box v-model="form.qualityAssuranceInfo.secondConfirm" class="flex-item" label=""></input-box>
                </div>
            </div>
            <div class="f-14 c-6 b" style="margin: 25px 0 0;"></div>
            <div class="flex-v-center">
                <input-box v-model="form.financialInfo.netWorth" type="number" label=""></input-box>
                <span class="mr-20"></span>
                <input-box v-model="form.financialInfo.liabilities" type="number" label=""></input-box>
                <span class="mr-20"></span>
                <input-box v-model="form.financialInfo.asset" type="number" label=""></input-box>
                <span></span>
            </div>
            <div class="f-14 c-6 b" style="margin: 10px 0 5px;"></div>
            <div class="flex-v-center" v-for="(item, i) in form.financialInfo.businessStateList" :key="item.id">
                <select-box v-model="item.year" class="mr-20" label="" :list="yearList"></select-box>
                <input-box v-model="item.sale" type="number" label=""></input-box>
                <span class="mr-20"></span>
                <input-box v-model="item.profit" type="number" label=""></input-box>
                <span class="mr-20"></span>
                <input-box class="flex-item mr-20" v-model="item.desc" label=""></input-box>
                <icon-btn small v-if="form.financialInfo.businessStateList.length > 1" @click="removeBusinessStateList(i)">close</icon-btn>
            </div>
            <div>
                <icon-btn v-if="form.financialInfo.businessStateList.length < 3" bg small v-tooltip="''" @click="addBusinessStateList">add</icon-btn>
            </div>
            <div class="f-14 c-6 b" style="margin: 10px 0 5px;"></div>
            <div class="flex-v-center" v-for="(item, i) in form.mainOrgan" :key="item.id">
                <input-box v-model="item.department" class="flex-item mr-20" label=""></input-box>
                <input-box v-model="item.leader" class="mr-20" label=""></input-box>
                <input-box v-model="item.phone" class="mr-20" label=""></input-box>
                <input-box v-model="item.fax" class="mr-20" label=""></input-box>
                <icon-btn small v-if="form.mainOrgan.length > 1" @click="removeOragn(i)">close</icon-btn>
            </div>
            <div>
                <icon-btn bg small v-tooltip="''" @click="addOragn">add</icon-btn>
            </div>
            <div class="flex-v-center" style="margin: 20px 0;">
                <btn big class="mr-20" @click="submit"></btn>
                <btn big flat bg class="mr-20" @click="cancel"></btn>
                <btn big flat @click="clear"></btn>
            </div>
        </div>
        <layer v-if="loading">
            <div class="abs t-center" style="padding: 20px;">
                <loading size="30"/>
                <div style="margin-top: 10px;"></div>
            </div>
        </layer>
    </div>
</template>

<script>
import form from "./form";
import parseJson from "@/utils/parseJson";
export default {
    name: "page-supplier-add",
    data() {
        const now = new Date();
        const year = now.getFullYear();
        const yearList = [];
        for (let i = 0; i < 4; i++) {
            yearList.push({ text: year - i, value: year - i + "" });
        }
        return {
            loading: false,
            workTypeList: [
                { text: "", value: 0 },
                { text: "", value: 1 },
                { text: "", value: 2 }
            ],
            natureList: [
                { text: "", value: 0 },
                { text: "", value: 1 },
                { text: "", value: 2 },
                { text: "", value: 3 },
                { text: "", value: 4 }
            ],
            yearList,
            form: JSON.parse(JSON.stringify(form))
        };
    },
    mounted() {
        let { id } = this.$route.query;
        if (id !== undefined && this.$route.name === "supplier-edit")
            this.getInfo(id);
    },
    methods: {
        addBusinessStateList() {
            this.form.financialInfo.businessStateList.push({
                desc: "",
                profit: "",
                sale: "",
                year: ""
            });
        },
        removeBusinessStateList(index) {
            this.form.financialInfo.businessStateList.splice(index, 1);
        },
        addOragn() {
            this.form.mainOrgan.push({
                department: "",
                fax: "",
                leader: "",
                phone: ""
            });
        },
        removeOragn(index) {
            this.form.mainOrgan.splice(index, 1);
        },
        getInfo(id) {
            this.$http
                .get(`/haolifa/supplier/info?id=${id}`)
                .then(res => {
                    res = parseJson(res, [
                        "credentialsInfo",
                        "financialInfo",
                        "mainOrgan",
                        "qualityAssuranceInfo",
                        "staffInfo"
                    ]);
                    // if (res.mainOrgan && res.mainOrgan.length) res.mainOrgan.forEach(item => { item.id = Math.random() })
                    // if (res.financialInfo && res.financialInfo.businessStateList && res.financialInfo.businessStateList.length) {
                    //   res.financialInfo.businessStateList.forEach(item => { item.id = Math.random().toString() })
                    // }
                    for (let key in this.form) {
                        this.form[key] = res[key];
                    }
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        submit() {
            const { form } = this;
            this.loading = true;
            this.$http
                .post(`/haolifa/supplier/${form.id ? "update" : "save"}`, form)
                .then(res => {
                    this.loading = false;
                    this.$toast("");
                    this.$router.replace("/supplier/addList");
                })
                .catch(e => {
                    this.loading = false;
                    this.$toast(e.msg || e.message);
                });
        },
        cancel() {
            this.$confirm({
                title: "",
                text: "",
                color: "red",
                btns: ["", ""],
                yes: () => {
                    this.$router.back();
                }
            });
        },
        clear() {
            this.$confirm({
                text: "",
                color: "darkorange",
                btns: ["", ""],
                yes: () => {
                    this.form = JSON.parse(JSON.stringify(form));
                }
            });
        }
    }
};
</script>

<style lang="less">
.page-supplier-add {
    padding: 30px 20px;
    .title {
        margin-bottom: 20px;
    }
    .credentials-form {
        .check-box {
            margin-bottom: 8px;
            width: 150px;
        }
    }
}
</style>
